<template>
    <div id="blog-detail">
        <navigation class="nav"/>
        <div class="detail-area area">
            <div class="head">
                <div class="title">{{detail.title}}</div>
                <div class="head-label">
                    <span>标签:{{detail.label}}</span>
                    <span>类型:{{detail.type}}</span>
                    <span>创建时间:{{detail.createTime}}</span>
                    <span>更新时间:{{detail.updateTime}}</span>
                </div>
            </div>
            <div v-html="detail.content"/>
        </div>
    </div>
</template>

<script>
    import navigation from "@/components/navigation_bar";
    import req from "@/utils/req";
    import api from "@/config/api";

    export default {
        data() {
            return {
                detail: {}
            }
        },
        mounted() {
            req(api.getArticle, {id: this.$route.query.id}).then(res => {
                this.detail = res.data;
            });
        },
        components: {
            navigation
        },
        beforeRouteEnter(to, from, next) {
            window.document.body.classList.add("blog-detail-background");
            next();
        },
        beforeRouteLeave(to, from, next) {
            window.document.body.classList.remove("blog-detail-background");
            next();
        }
    }
</script>

<style lang="scss">
    .blog-detail-background {
        @include background("../../assets/img/b3.jpg", $width, $repeat);
    }
    #blog-detail {
        width: 100%;
        @include align(24, $column);
    }
    #blog-detail {
        .detail-area {
            width: $areaWidth;
            margin-bottom: 3rem;
        }
        .head {
            width: 100%;
            padding-bottom: 1rem;
            border-bottom: 1px solid $borderColor;
            @include align(24, $column);
        }
        .title {
            @include font(1rem, #333, bold);
        }
        .head-label {
            margin-top: 1rem;
            @include align(4);
        }
        .head-label > span {
            margin-right: 1rem;
        }
    }
</style>
